import * as React from 'react';
import {
  Segment, Header, Divider, Container
} from 'semantic-ui-react';

const pStyle = {
  fontSize: '1.33em',
};

class Roster extends React.Component {
  render() {
    return (
      <Segment style={{ padding: '8em 0em' }} vertical={true}>
        <Container text={true}>
          <Header as="h3" style={{ fontSize: '2em' }}>About me</Header>
          <div style={{textAlign: 'justify', textJustify: 'inter-word'}}>
            <p style={pStyle}>
              I am the associated director of the Knowledge Systems Group (KSG)
              at Dana-Farber Cancer Institute (DFCI). I am interested in good software,
              machine learning, and computational biology. Translating ideas into action,
              and building teams make me happy.
            </p>
          </div>
          <Divider
            as="h4"
            className="header"
            horizontal={true}
            style={{ margin: '3em 0em', textTransform: 'uppercase' }}
          >
            <a href="#">Current</a>
          </Divider>
          <div style={{textAlign: 'justify', textJustify: 'inter-word'}}>
            <p style={pStyle}>
              I am part of a team building the 
              Cancer Immunologic Data Commons (CIDC) which is part of an NCI moonshot 
              effort to bring data from many cancer immunotherapy clinical trials into 
              a harmonized repository. This effort will enable integrative biomarker analysis 
              across diverse data modalities. As the chief software architect of the project, 
              I am guiding the development of the software following a cloud native strategy. 
              Additionally I also co lead the development of MatchMiner, a clinical 
              decision support platform to match cancer patients to precision medicine clinical 
              trials. MatchMiner is actively used by oncologists at DFCI in conjunction 
              with the institutes enterprise genomic profiling effort. Finally I also advise 
              on the development of the open source cancer genomics research platform, 
              cBioPortal. The cBioPortal has been cited in thousands of publications and has 
              over 300 unique visitors every day.
            </p>
          </div>
          <Divider
            as="h4"
            className="header"
            horizontal={true}
            style={{ margin: '3em 0em', textTransform: 'uppercase' }}
          >
            <a href="#">Past</a>
          </Divider>
          <div style={{textAlign: 'justify', textJustify: 'inter-word'}}>
            <p style={pStyle}>
              Prior to working at DFCI I was a co-founder of a cloud bioinformatics startup 
              called Smpl Bio which provided automated workflows for single cell genomics. 
              While at Smpl Bio I lead the algorithms and systems development, raised funding, 
              managed a team of 5 and participated in all aspects of the company. I received my Ph.D. 
              in Computer Science at the University of Connecticut. 
              I focused on developing scalable optimization algorithms for problems 
              in computational genomics including de - novo genome assembly. 
              My code was used in the publication of the tammar wallaby genome.
            </p>
          </div>
        </Container>
      </Segment>
    );
  }
}

export default Roster;


